<template>
  <Header v-if="!hideHeaderRoutes.includes($route.name)" />
  <router-view />
</template>
<script>
import Header from './components/Header.vue';
import { useRoute } from 'vue-router';
import { watch } from 'vue';
import axios from 'axios';
import store from './scripts/store';

export default {
  name: 'App',
  components: {
    Header
  },
  setup() {
    const check = () => {
      axios.get('/api/account/check')
      .then((response)=>{
        store.commit('setAccount', response.data || 0);
      })
    };

    const route = useRoute();

    watch(route, () => {
      check();
    });
  },
  data() {
    return {
      hideHeaderRoutes: ['login', 'signup']
    };
  },
}
</script>

