<template>
  <header class="nexon-header">
    <div class="header-container">
      <!-- 햄버거 메뉴 -->
      <div class="menu-toggle" @click="toggleSideMenu">
        <span :class="{ active: isSideMenuOpen }"></span>
        <span :class="{ active: isSideMenuOpen }"></span>
        <span :class="{ active: isSideMenuOpen }"></span>
      </div>

      <!-- 로고 -->
      <div class="logo" @click="$router.push('/')"></div>

      <!-- 우측 메뉴 -->
      <div class="header-right">
        <div class="user-menu">
          <span class="notification-icon">검색</span>
          <router-link to="/login" class="login-btn" v-if="!$store.state.account.id">로그인</router-link>
          <a to="/login" class="login-btn" @click="logout()" v-else>로그아웃</a>
          <router-link to="/login" class="mobile-user-icon" v-if="!$store.state.account.id">👤</router-link>
          <router-link to="/mypage" class="mobile-user-icon" v-else>👤</router-link>
        </div>
      </div>
    </div>

    <!-- 사이드 메뉴 오버레이 -->
    <div class="side-menu-overlay" :class="{ active: isSideMenuOpen }" @click="closeSideMenu"></div>

    <!-- 사이드 메뉴 -->
    <nav class="side-menu" :class="{ active: isSideMenuOpen }">
      <div class="side-menu-content">
        <div class="menu-header">
          <button class="close-btn" @click="closeSideMenu">
            <span class="close-icon">←</span>
          </button>
        </div>

        <div class="menu-section">
          <ul class="menu-list">
            <li class="menu-item" @click="navigateTo('notice')">공지사항</li>
            <li class="menu-item" @click="navigateTo('board')">자유게시판</li>
            <li class="menu-item" @click="navigateTo('suggest')">건의사항</li>
          </ul>
        </div>

        <div class="menu-divider"></div>

        <div class="menu-section">
          <ul class="menu-list">
            <li class="menu-item" @click="navigateTo('psychology')">심리테스트</li>
            <li class="menu-item" @click="navigateTo('fortune')">사주/운세풀이</li>
            <li class="menu-item" @click="navigateTo('quiz')">맞추기퀴즈</li>
            <li class="menu-item" @click="navigateTo('etc')">기타</li>
          </ul>
        </div>

        <div class="language-section">
          <div class="language-flags">
            <span class="flag-item" @click="changeLanguage('ko')" title="한국어">🇰🇷</span>
            <span class="flag-item" @click="changeLanguage('en')" title="English">🇺🇸</span>
            <span class="flag-item" @click="changeLanguage('ja')" title="日本語">🇯🇵</span>
            <span class="flag-item" @click="changeLanguage('zh')" title="中文">🇨🇳</span>
          </div>
        </div>
      </div>
    </nav>
  </header>
</template>

<script>
import router from '@/router';
import store from '@/scripts/store';
import axios from 'axios';

export default {
  name: 'Header',
  setup() {
    const logout = () => {
      axios.get('/api/user/logout')
        .then((response) => {
          store.commit('setAccount', 0);
          router.push('/');
        })
    }

    return { logout };
  },
  data() {
    return {
      isSideMenuOpen: false
    }
  },
  methods: {
    toggleSideMenu() {
      this.isSideMenuOpen = !this.isSideMenuOpen;
    },
    closeSideMenu() {
      this.isSideMenuOpen = false;
    },
    navigateTo(category) {
      // 라우터 네비게이션 로직
      console.log(`Navigate to: ${category}`);
      this.$router.push(`/${category}`);
      this.closeSideMenu();
    },
    changeLanguage(lang) {
      // 언어 변경 로직
      console.log(`Change language to: ${lang}`);
      // 여기에 i18n 또는 언어 변경 로직 추가
      this.closeSideMenu();
    }
  },
  mounted() {
    // ESC 키로 메뉴 닫기
    document.addEventListener('keydown', (e) => {
      if (e.key === 'Escape' && this.isSideMenuOpen) {
        this.closeSideMenu();
      }
    });
  }
}
</script>

<style scoped>
.nexon-header {
  background-color: #fff;
  border-bottom: 1px solid #e0e0e0;
  height: 60px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
}

.header-container {
  max-width: 1200px;
  margin: 0 auto;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 20px;
  position: relative;
}

.menu-toggle {
  display: flex;
  flex-direction: column;
  cursor: pointer;
  margin-right: 8px;
  padding: 8px;
  z-index: 2;
}

.menu-toggle span {
  width: 18px;
  height: 2px;
  background-color: #333;
  margin: 2px 0;
  transition: 0.3s ease;
  transform-origin: center;
}

.menu-toggle span.active:nth-child(1) {
  transform: rotate(45deg) translate(5px, 5px);
}

.menu-toggle span.active:nth-child(2) {
  opacity: 0;
}

.menu-toggle span.active:nth-child(3) {
  transform: rotate(-45deg) translate(7px, -6px);
}

.logo {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

.logo::after {
  content: "PIKOLO";
  font-size: 24px;
  font-weight: bold;
  color: #333;
  letter-spacing: 2px;
}

.header-right {
  display: flex;
  align-items: center;
  margin-left: auto;
  z-index: 2;
}

.user-menu {
  display: flex;
  align-items: center;
  gap: 16px;
}

.notification-icon {
  font-size: 18px;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  cursor: pointer;
  padding: 8px;
  border-radius: 20%;
  transition: background-color 0.2s ease;
}

.notification-icon:hover {
  background-color: #f5f5f5;
}

.login-btn {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: 0.2s ease;
  text-decoration: none;
}

.login-btn:hover {
  background-color: #0056b3;
  transform: translateY(-1px);
}

.mobile-user-icon {
  font-size: 18px;
  cursor: pointer;
  padding: 8px;
  border-radius: 50%;
  display: none;
  transition: background-color 0.2s ease;
}

.mobile-user-icon:hover {
  background-color: #f8f9fa;
}

.side-menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 998;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s ease;
}

.side-menu-overlay.active {
  opacity: 1;
  visibility: visible;
}

.side-menu {
  position: fixed;
  top: 0;
  left: -300px;
  width: 300px;
  height: 100%;
  background-color: #fff;
  z-index: 999;
  transition: left 0.3s ease;
  box-shadow: 2px 0 10px rgba(0, 0, 0, 0.1);
}

.side-menu.active {
  left: 0;
}

.side-menu-content {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.menu-header {
  padding: 20px;
  border-bottom: 1px solid #e0e0e0;
  display: flex;
  align-items: center;
  min-height: 60px;
}

.close-btn {
  background: none;
  border: none;
  cursor: pointer;
  padding: 8px 12px;
  border-radius: 8px;
  display: flex;
  align-items: center;
}

.close-btn:hover {
  background-color: #f0f0f0;
}

.close-icon {
  font-size: 20px;
  color: #333;
  font-weight: bold;
}

.menu-section {
  padding: 0 0px;
}

.menu-list {
  list-style: none;
}

.menu-item {
  padding: 15px 0;
  font-size: 16px;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  color: #333;
  cursor: pointer;
  border-bottom: 1px solid #f0f0f0;
  transition: 0.2s ease;
}

.menu-item:hover {
  color: #007bff;
  background-color: #f0f0f0;
  padding-left: 10px;
}

.menu-item:last-child {
  border-bottom: none;
}

.menu-divider {
  height: 1px;
  background-color: #e0e0e0;
  margin: 20px;
}

.language-section {
  margin-top: auto;
  padding: 20px;
  border-top: 1px solid #e0e0e0;
}

.language-flags {
  display: flex;
  justify-content: space-around;
}

.flag-item {
  font-size: 24px;
  cursor: pointer;
  padding: 8px;
  border-radius: 8px;
  transition: 0.2s ease;
}

.flag-item:hover {
  background-color: #f0f0f0;
  transform: scale(1.1);
}

@media screen and (max-width: 768px) {
  .header-container {
    padding: 0 15px;
  }

  .user-menu {
    gap: 12px;
  }

  .side-menu {
    width: 280px;
    left: -280px;
  }
}

@media screen and (max-width: 480px) {
  .login-btn {
    display: none;
  }

  .mobile-user-icon {
    display: inline-block;
  }

  .user-menu {
    gap: 8px;
  }

  .side-menu {
    width: 250px;
    left: -250px;
  }

  .language-flags {
    justify-content: space-between;
  }

  .flag-item {
    font-size: 20px;
  }

  .menu-header {
    padding: 15px;
    min-height: 50px;
  }

  .close-icon {
    font-size: 18px;
  }
}
</style>
