// router/index.js
import { createRouter, createWebHistory } from "vue-router";

const routes = [
  {
    path: "/",
    name: "main",
    component: () => import("../pages/Home.vue"),
    meta: {
      title: '피콜로 - 우리 모두의 놀이터',
    }
  },
  {
    path: "/login",
    name: "login",
    component: () => import("../pages/Login.vue"),
    meta: {
      title: '피콜로 - 로그인',
    }
  },
  {
    path: "/signup",
    name: "signup",
    component: () => import("../pages/Signup.vue"),
    meta: {
      title: '피콜로 - 회원가입',
    }
  },
  {
    path: "/notice",
    name: "notice",
    component: () => import("../pages/Notice.vue"),
    meta: {
      title: '피콜로 - 공지사항',
    }
  },
  {
    path: "/board",
    name: "board",
    component: () => import("../pages/Board.vue"),
    meta: {
      title: '피콜로 - 자유게시판',
    }
  },
  {
    path: "/suggest",
    name: "suggest",
    component: () => import("../pages/Suggest.vue"),
    meta: {
      title: '피콜로 - 건의사항',
    }
  },
  {
    path: "/mindtest",
    name: "mindtest",
    component: () => import("../pages/contents/MindTest.vue"),
    meta: {
      title: '피콜로 - 심리테스트',
    }
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes,
  scrollBehavior() {
    return { top: 0 }
  }
});

export default router;
